import cellIconList from '@/components/mycom/cellIconList.vue';
import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue';
import bassUrl from '@/common/js/config.js';
let qrcode;
export default {
	components: {
		cellIconList,
		tkiQrcode
	},
	data() {
		return {
			dataObject: {
				userIcon: '', //头像
				nickName: '', //昵称
				worksType:'', //是否求职:0-否，1-是
				qrcode: '', //二维码
				indoor: 0, //是否内部员工:0-否，1-是
				inverteCode: '' //邀请码
			},
			image: '',
			text:'',
			size: 314
		};
	},
	onLoad() {
		this.text = `${bassUrl.bass}/share/h5/#/?id=${getApp().globalData.uid}`;
	},
	onShow() {
		this.loadData();
		if(this.$wechat.isWechat()){
		    this.$wechat.wxShare(); 
		}
	},
	methods: {
		qrcode2307Complete(e) {
			this.image = e;
		},
		loadData() {
			let parmas = {
				uid: getApp().globalData.uid
			};
			console.log(parmas);
			this._reqw
				.ipost(parmas, 'personInfo')
				.then(res => {
					res.result == 0 ? (this.dataObject = res) : this.$api.tip(res.resultNote);
				})
				.catch(err => {});
		},
		personClick(k) {
			this.$api.navTo(`/pages/personInfo?name=${this.nickName}`);
		},
		cellClick(e) {
			console.log(e);
			switch (e) {
				case 0:
					this.$api.navTo(`/pages/myActive`);
					break;
				case 1:
					this.$api.navTo('/pages/mybiographical');
					break;
				case 2:
					this.$api.navTo('/pages/myStore');
					break;
				case 3:
					this.$api.navTo(`/pages/mysalary`);
					break;
				case 4:
					// 我的奖金
					this.$api.navTo('/pages/mybonus');
					break;
				//  个人资料
				case 5:
					this.$api.navTo('/pages/mySetUp');
					break;
			}
		}
	}
};
